.tab {
	border: none;
	background: none;
	all: unset;
	padding: 0.6rem 2.5rem 0.6rem 2.5rem;
	min-width: 2rem;
	font-size: 0.9rem;
	color: #555;
	position: relative;
	white-space: nowrap;
	user-select: none;
	outline: none;
	margin-left: -1rem;
	margin-right: -1rem;
	border-top-left-radius: 0.6rem;
	border-top-right-radius: 0.6rem;
	clip-path: polygon(0px 101%, 3px 99%, 6px 98%, 9px 92%, 30px 14%, 33px 6%, 36px 2%, 39px 0%, calc(100% - 39px) 0%, calc(100% - 36px) 2%, calc(100% - 33px) 6%, calc(100% - 30px) 14%, calc(100% - 9px) 92%, calc(100% - 6px) 98%, calc(100% - 3px) 99%, 100% 101%);

	&::before {
		left: calc(-1rem - 5px);
		border-bottom-color: white;
		transform: rotate(-45deg);
	}

	&::after {
		right: calc(-1rem - 5px);
		transform: rotate(45deg);
	}

	&:focus,
	&:active,
	&:hover {
		box-shadow: none;
		background: #fafafa;
		border-bottom-color: #fafafa;
	}

	&:focus,
	&:active,
	&:hover {
		&::before,
		&::after {
			border-bottom-color: #fafafa;
		}

		.title {
			&::after {
				background: linear-gradient(90deg, rgba(255, 255, 255, 0), #fafafa);
			}
		}
	}
}

.title {
	width: calc(100% - 2.4rem);
	overflow: hidden;
	display: block;
	padding-left: 1.2rem;
	display: flex;
	align-items: center;
	margin-right: 2.5rem;

	&::before {
		content: '';
		display: block;
		position: absolute;
		left: 2.3rem;
		width: 1rem;
		height: 1rem;
		background-size: cover;
	}

	&::after {
		content: '';
		position: absolute;
		height: 1rem;
		width: 2rem;
		right: 3.5rem;
	}
}

.flex::before {
	background-image: url('../../assets/flex.ico');
}

.grid::before {
	background-image: url('../../assets/grid.ico');
}

.active {
	background: white;
	box-shadow: 0 0 15px #ddd;
	z-index: 1;

	&::before,
	&::after {
		border-bottom-color: white;
	}

	.title::after {
		background: linear-gradient(90deg, rgba(255, 255, 255, 0), #fff);
	}
}

.inactive {
	&::before,
	&::after {
		border-bottom-color: #eee;
	}

	.title::after {
		background: linear-gradient(90deg, rgba(255, 255, 255, 0), #eee);
	}
}

.close {
	cursor: pointer;
	position: absolute;
	display: block;
	height: 1rem;
	width: 1rem;
	border-radius: 50%;
	top: 50%;
	right: 1.7rem;
	transform: translate(-50%, -50%);
	z-index: 1;

	&:hover {
		background: #ddd;
	}

	&::before,
	&::after {
		content: '';
		display: block;
		top: 50%;
		left: 50%;
		width: 0.7rem;
		height: 0.1rem;
		background: #555;
		position: absolute;
	}

	&::before {
		transform: translate(-49.5%, -1px) rotate(45deg);
	}
	&::after {
		transform: translate(-50.5%, -1px) rotate(-45deg);
	}
}
